.App {
  text-align: center;
  background-color: #000b16;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #455472;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Greeting {
  padding-top: 200px;
  min-height: 600px;
  color: white;
  margin-bottom: 150px;
}

.project-title {
  color: rgb(255, 255, 231);
}

.blink {
  border-bottom: 2px solid #78d68b;
  margin-left: 0%;
  width: 100%;
  animation: blink 10s infinite, slidein 3s;
}

.blink:hover {
  animation-duration: 1s;
}

@keyframes blink {
  25% { border-color: #5890c5; }
  50% { border-color: #af3a3a; }
  75% { border-color: orange; }
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}
